import { Card, Breadcrumb, Form, Button, Radio, Input, Upload, Space, Select, message } from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'
import './index.scss'
// 
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'

import { addArticleService, getArticleDetailService, updateArticleService } from '@/apis/article.js'

import { useState } from 'react'
import useChannel from '@/hooks/useChannel'
import { useEffect } from 'react'

const { Option } = Select

const Publish = () => {
  // 
  // hooks
  const { channels } = useChannel()

  // 
  const onFinish = async (formValues) => {
    // typeimageList
    if (imageList.length !== type) return message.warning('')
    const reqData = {
      ...formValues,
      cover: {
        type, // 
        images: imageList.map((item) => {
          // response
          if (item.response) {
            return item.response.data.url
          } else {
            // 
            return item.url
          }
        }) // 
      }
    }
    if (articleId) {
      // 
      try {
        await updateArticleService({
          ...reqData,
          id: articleId
        })
        message.success('')
        navigate('/article')
      } catch (error) {
        console.log(error)
        message.error('')
      }
    } else {
      // 
      try {
        await addArticleService(reqData)
        message.success('')
        navigate('/article')
      } catch (error) {
        console.log(error)
        message.error('')
      }
    }
  }
  // 
  const [imageList, setImageList] = useState([])
  const onUploadChange = (value) => {
    // console.log('',value)
    setImageList(value.fileList)
  }
  // 
  const navigate = useNavigate()

  // 
  const [type, setType] = useState(0)
  const onChangeType = (e) => {
    // console.log(e.target.value)
    setType(e.target.value)
  }

  // 
  // id
  const [searchParams] = useSearchParams()
  const articleId = searchParams.get('id')

  // 
  const [form] = Form.useForm()
  useEffect(() => {
    const getArticleDetail = async () => {
      try {
        const res = await getArticleDetailService(articleId)
        // 
        form.setFieldsValue({
          ...res.data,
          type: res.data.cover.type
        })

        // 
        setType(res.data.cover.type)
        setImageList(
          res.data.cover.images.map((item) => ({
            url: item
          }))
        )
      } catch (error) {
        console.log(error)
      }
    }
    articleId && getArticleDetail()
  }, [articleId, form])
  return (
    <div className="publish">
      <Card
        title={
          <Breadcrumb
            items={[
              { title: <Link to={'/'}></Link> },
              { title: articleId ? '' : '' }
            ]}
          />
        }
      >
        <Form
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 16 }}
          initialValues={{ type: 0 }}
          onFinish={onFinish}
          form={form}
        >
          <Form.Item
            label=""
            name="title"
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" style={{ width: 400 }} />
          </Form.Item>
          <Form.Item
            label=""
            name="channel_id"
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder="" style={{ width: 400 }}>
              {channels.map((item) => (
                <Option key={item.id} value={item.id}>
                  {item.name}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item label="">
            <Form.Item name="type">
              <Radio.Group onChange={onChangeType}>
                <Radio value={1}></Radio>
                <Radio value={3}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            </Form.Item>
            {/* 
                listType: 
                showUploadList: 
            */}
            {type > 0 && (
              <Upload
                name="image"
                listType="picture-card"
                showUploadList
                action={'http://geek.itheima.net/v1_0/upload'}
                onChange={onUploadChange}
                maxCount={type}
                fileList={imageList}
              >
                <div style={{ marginTop: 8 }}>
                  <PlusOutlined />
                </div>
              </Upload>
            )}
          </Form.Item>
          <Form.Item
            label=""
            name="content"
            rules={[{ required: true, message: '' }]}
          >
            {/*  */}
            <ReactQuill className="publish-quill" theme="snow" placeholder="" />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 4 }}>
            <Space>
              <Button size="large" type="primary" htmlType="submit">
                
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default Publish
